<script lang="coffeescript">


  import { createEventDispatcher } from "svelte"
  
  import {
    FlexColumn, 
    FlexColumnCell, 
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"
  import { ButtonIcon } from "braintapper-svelte-button";
  import { IconSvg, IconToggleSvg } from "braintapper-svelte-icon"
  import { 
    debug, 
    DateCalendar, 

    FormatRelativeDate,
    
  } from "../../../Braintapper"

  dispatch = createEventDispatcher()

  export value = undefined
  export isShowing = undefined




  clear = ()->
    dispatch "tippy-event", { event: "clear", value: null }

  setDate = (e)->
    event = { event: "set", value: e.detail }
    if Object.isDate(e.detail)
      console.error "_DueDate.svelte setDate is a date"
      value = e.detail.format("{yyyy}-{MM}-{dd}") # refresh the value
    else
      console.error "_DueDate.svelte setDate is not a date"
      value = e.detail

    dispatch "tippy-event", event



</script>




<FlexColumn>
  <FlexColumnCell>
    <div relative-date-container>
      <FlexRow>
        <FlexRowCell flex="flex">
          <div relative-date>
            <span>
            {#if value == null}
              No date assigned
            {:else}
              <FormatRelativeDate {value}/>
            {/if}
            </span>
          </div>
        </FlexRowCell>
        <FlexRowCell>
          <ButtonIcon on:click={clear}>
            <IconSvg icon="clear"/>
          </ButtonIcon>
        </FlexRowCell>
      </FlexRow>
    </div>
  </FlexColumnCell>
  <FlexColumnCell>
    <DateCalendar {value} bind:isShowing={isShowing} on:change={setDate}/>
  </FlexColumnCell>
</FlexColumn>



<style lang="sass">
  [relative-date-container]
    
    padding: 8px 8px 0px 8px

    [relative-date]
      padding-top: 2px
      padding-left: 4px
      font-size: 14px
      line-height: 24px
      text-align: middle
      font-weight: bold

</style>    
